const nvhtml = require('nvhtml')

function rm_hw(s) {
    s=s.replace(/[\r\n]/g,'')
    let rt = new nvhtml.Html(s)
    let sdfs = rt.$sdfs()
    let svg = sdfs.filter(r=>r.tag==='svg')[0]
    delete svg.attribs.width
    delete svg.attribs.height
    svg =svg.stringify()
    svg=svg.replace(/[\r\n]/g,'')
    return(svg)
}


module.exports = {
    rm_hw
}
